@use "base/variables" as v;
@use "base/mixins" as m;

.navegacion {
  width: 100%;
display: flex;
flex-direction: column;
align-items: center;
gap: 2rem;

@include m.desktop { 
    width: auto;
    flex-direction: row;
    

 }


  &__link {
    font-size: 2rem;
    font-weight: 700;
    color: v.$negro;

    &--registrar {
      @include m.boton(v.$primario, v.$blanco);
    }
  }
}
