@use 'base/variables' as v; 
@use 'base/mixins' as m;

.footer{
 background-color: v.$secundario;
 padding-top: v.$separacion ;

 &__grid{

    @include m.tablet { 
        @include m.grid(3,4rem);
     }
 }

 &__box{
    margin-bottom: v.$separacion;
    &:last-of-type{
        margin-bottom: 0;
    }
 }

 &__heading{
    color: v.$blanco;
    margin: 0 ;
    text-align: center;
    @include m.tablet { 
        text-align: left;
     }
 }

 &__nav{
    display: flex;
    flex-direction: column;
  
 }

 &__nav,
 &__texto{
    margin-top: 2rem;
 }

 &__link,
 &__texto{
    text-align: center;
    font-size: 2rem;
    line-height: 1.8;
    color: v.$blanco ; 
    @include m.tablet { 
        text-align: left;
     }
 }

 &__copy{
    background-color: v.$primario;
    text-align: center;
    color: v.$blanco;
    margin: 0;
    padding: 2rem;
 }
}